body{ height: 100vh; background: #102C57;}
#root{ height: 800px; position: relative;}
.introPage{ 
    width: 100%; height: 100vh; background: #ededed;
}
.introPage>a{
    width: 900px; height: 200px; 
    background: url('../public/imgs/index.png') no-repeat;
    text-indent: -99999px; 
    position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);
}
.prev, .next{ 
    background: url('../public/imgs/prev.png') no-repeat center/cover;
    width: 30px; height: 30px;
}
.next{ background-image: url('../public/imgs/next.png');}
.edit, .delete{ 
    background: url('../public/imgs/edit.png') no-repeat center/cover;
    width: 30px; height: 30px; 
}
.delete{ background-image: url('../public/imgs/delete.png'); width: 28px; height: 28px;}

.text_area{
    width:360px; height:26px; border:0 none; 
    border-bottom: 1px solid #102C57; background: transparent;
    color: #777; padding-left: 8px; padding-bottom: 2px; font-size: 14px;
}
.input_text{ 
    border:0 none; border-bottom: 1px solid #102C57; background: transparent;
    padding-bottom: 6px; padding-left: 4px; text-align: center;
    width: 360px; font-size: 14px;
}
.edit_plus, .submit{ width: 26px; height: 26px; background: url('../public/imgs/plus.png') no-repeat center/cover;}
.edit_plus{ background-image: url('../public/imgs/check.png');}
.box{ border-left:1px solid #102C57; border-top: 1px solid #102C57;}
.box>div{ border-right: 1px solid #102C57; border-bottom: 1px solid #102C57;}